import React, { useState, useEffect } from 'react';
import { Typography, Container, Grid, Paper, Box, LinearProgress, Stack } from '@mui/material';
import BatteryChargingFullIcon from '@mui/icons-material/BatteryChargingFull';
import SolarPowerIcon from '@mui/icons-material/SolarPower';
import ElectricMeterIcon from '@mui/icons-material/ElectricMeter';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';

// Register ChartJS components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const CACHE_KEY = 'energyData';
const MAX_HISTORY_POINTS = 24; // 24 hours of data

// Simple time formatter
const formatTime = (date) => {
  const hours = date.getHours().toString().padStart(2, '0');
  const minutes = date.getMinutes().toString().padStart(2, '0');
  return `${hours}:${minutes}`;
};

const EnergyManagement = () => {
  // Simulated live data states
  const [batteryLevel, setBatteryLevel] = useState(75);
  const [solarInput, setSolarInput] = useState(850);
  const [powerConsumption, setPowerConsumption] = useState(420);
  const [timeRemaining, setTimeRemaining] = useState(14.5);
  const [historicalData, setHistoricalData] = useState(() => {
    const cached = localStorage.getItem(CACHE_KEY);
    return cached ? JSON.parse(cached) : {
      battery: [],
      solar: [],
      consumption: [],
      timestamps: []
    };
  });

  // Update and cache historical data
  useEffect(() => {
    const updateHistory = () => {
      const timestamp = formatTime(new Date());
      
      setHistoricalData(prev => {
        const newData = {
          battery: [...prev.battery, batteryLevel],
          solar: [...prev.solar, solarInput],
          consumption: [...prev.consumption, powerConsumption],
          timestamps: [...prev.timestamps, timestamp]
        };

        // Keep only last MAX_HISTORY_POINTS
        if (newData.battery.length > MAX_HISTORY_POINTS) {
          newData.battery = newData.battery.slice(-MAX_HISTORY_POINTS);
          newData.solar = newData.solar.slice(-MAX_HISTORY_POINTS);
          newData.consumption = newData.consumption.slice(-MAX_HISTORY_POINTS);
          newData.timestamps = newData.timestamps.slice(-MAX_HISTORY_POINTS);
        }

        // Cache the data
        localStorage.setItem(CACHE_KEY, JSON.stringify(newData));
        return newData;
      });
    };

    // Update live values and history every 3 seconds
    const interval = setInterval(() => {
      setBatteryLevel(prev => Math.min(100, Math.max(0, prev + (Math.random() - 0.5) * 2)));
      setSolarInput(prev => Math.max(0, prev + (Math.random() - 0.5) * 50));
      setPowerConsumption(prev => Math.max(0, prev + (Math.random() - 0.5) * 30));
      setTimeRemaining(prev => Math.max(0, prev + (Math.random() - 0.5)));
      updateHistory();
    }, 3000);

    return () => clearInterval(interval);
  }, [batteryLevel, solarInput, powerConsumption]);

  const chartData = {
    labels: historicalData.timestamps,
    datasets: [
      {
        label: ' (%)',
        data: historicalData.battery,
        borderColor: '#4caf50',
        tension: 0.4,
        fill: false
      },
      {
        label: '  (W)',
        data: historicalData.solar,
        borderColor: '#ffa726',
        tension: 0.4,
        fill: false
      },
      {
        label: ' (W)',
        data: historicalData.consumption,
        borderColor: '#ef5350',
        tension: 0.4,
        fill: false
      }
    ]
  };

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: false
      }
    },
    scales: {
      y: {
        beginAtZero: true
      }
    }
  };

  const getBatteryColor = (level) => {
    if (level > 60) return '#4caf50';
    if (level > 30) return '#ff9800';
    return '#f44336';
  };

  const MetricCard = ({ icon, title, value, unit, color, progress }) => (
    <Paper
      elevation={3}
      sx={{
        p: 3,
        height: '100%',
        borderRadius: '12px',
        transition: 'transform 0.2s',
        '&:hover': {
          transform: 'translateY(-4px)'
        }
      }}
    >
      <Stack spacing={2}>
        <Stack direction="row" spacing={1} alignItems="center">
          <Box sx={{ color: color || 'primary.main' }}>
            {icon}
          </Box>
          <Typography variant="h6" color="text.secondary">
            {title}
          </Typography>
        </Stack>
        
        <Typography variant="h4" sx={{ fontWeight: 'bold', color: color || 'text.primary' }}>
          {value}
          <Typography component="span" variant="h6" color="text.secondary">
            {' '}{unit}
          </Typography>
        </Typography>

        {progress && (
          <Box sx={{ width: '100%', mt: 1 }}>
            <LinearProgress 
              variant="determinate" 
              value={progress} 
              sx={{ 
                height: 10, 
                borderRadius: 5,
                bgcolor: 'rgba(0,0,0,0.1)',
                '& .MuiLinearProgress-bar': {
                  bgcolor: color,
                  borderRadius: 5
                }
              }} 
            />
          </Box>
        )}
      </Stack>
    </Paper>
  );

  return (
    <Box sx={{ p: 3, pb: 10 }}>
      <Typography variant="h4" sx={{ mb: 3, color: '#2c3e50', fontWeight: 'bold' }}>
         
      </Typography>

      <Grid container spacing={3}>
        {/* Battery Level */}
        <Grid item xs={12} md={6} lg={3}>
          <MetricCard
            icon={<BatteryChargingFullIcon sx={{ fontSize: 40 }} />}
            title=" "
            value={batteryLevel.toFixed(1)}
            unit="%"
            color={getBatteryColor(batteryLevel)}
            progress={batteryLevel}
          />
        </Grid>

        {/* Solar Input */}
        <Grid item xs={12} md={6} lg={3}>
          <MetricCard
            icon={<SolarPowerIcon sx={{ fontSize: 40 }} />}
            title=" "
            value={solarInput.toFixed(0)}
            unit="W"
            color="#ffa726"
          />
        </Grid>

        {/* Power Consumption */}
        <Grid item xs={12} md={6} lg={3}>
          <MetricCard
            icon={<ElectricMeterIcon sx={{ fontSize: 40 }} />}
            title=""
            value={powerConsumption.toFixed(0)}
            unit="W"
            color="#ef5350"
          />
        </Grid>

        {/* Time Remaining */}
        <Grid item xs={12} md={6} lg={3}>
          <MetricCard
            icon={<AccessTimeIcon sx={{ fontSize: 40 }} />}
            title=" "
            value={timeRemaining.toFixed(1)}
            unit=""
            color="#66bb6a"
          />
        </Grid>

        {/* Historical Graph */}
        <Grid item xs={12}>
          <Paper 
            elevation={3} 
            sx={{ 
              p: 3, 
              borderRadius: '12px',
              height: '400px'
            }}
          >
            <Typography variant="h6" color="text.secondary" gutterBottom>
                (24 )
            </Typography>
            <Box sx={{ height: 'calc(100% - 40px)' }}>
              <Line data={chartData} options={chartOptions} />
            </Box>
          </Paper>
        </Grid>
      </Grid>
    </Box>
  );
};

export default EnergyManagement;